import styles from './Search.module.css';
import Button from '../Button/Button';
import FilerButton from './filterButton/filterButton';

const Search = (props)=>{
    const filterButtons = Object.keys(props.filters).map(key=>{
        return(
            <FilerButton
                    className = {styles.FilterButton} 
                    key={key}
                    selected={props.filters[key]} 
                    onClick={props.filterButtonClickHandler}
                    filterValue = {key}
                    >{key}</FilerButton>
        )
    })
    return(
        <div className={styles.Search}>
            <div className={styles.SearchBar}>
                <form onSubmit={props.onSearchHandler}>
                    <input type="text" name="searchValue" value={props.inputValue} onChange={props.onChangeHandler} placeholder="Search..." required/>
                    <Button type='submit'>Search</Button>
                </form>
            </div>
            <div className={styles.filters}>
                {filterButtons}
            </div>
        </div>
    );
}

export default Search;