import React from 'react';
import styles from './filterButton.module.css';

const Button = (props)=>{
    return(
        <button className={styles.filerButton} style={(props.selected)?{backgroundColor : '#1f25d1' , color : 'white'} : {}} onClick={props.onClick} value={props.filterValue}>{props.children}</button>
    );
}

export default Button;